.scroll-nav {
    background-color: #f8f9fa;
    border: 1px solid #a2a9b1;
    padding: 15px 10px;
    position: fixed;
    top: 40vh;
    @media screen and (max-width: 1200px) {
		display: none;
    }
    max-width: 400px;
    min-width: 300px;
  }

.scroll-nav__list {
    margin: 0;
    padding-left: 1.4em;
    list-style-type: upper-roman;
}

.scroll-nav__item {
    margin-bottom: 5px;
}

.scroll-nav__item > a {
    // margin-bottom: 5px;
    text-decoration: none;

}

.scroll-nav__item--active > a {
    padding: 5px;
    font-weight: bold;
    position: relative;
    // color: rgba(221, 221, 221, 0.8);
    background: rgb(241, 234, 128);
    text-decoration: none;
    // @include transition(0.5s);
    transition-duration: 0.7s;
    &:hover{
        color:rgba(168, 168, 168,0.8);
        text-decoration: none;
    }
}


.scroll-nav__link {
    color: #000000;
    text-decoration: none;
    &:hover, &:active, &:visited, &:focus{
        text-decoration: none;
    }
}

.scroll-nav__link:hover {
    text-decoration: none;
}

.scroll-nav__sub-link {
    color: #000000;
    text-decoration: none;
    &:hover, &:active, &:visited, &:focus{
        text-decoration: none;
    }
}

.scroll-nav__sub-link:hover {
    text-decoration: none;
}